import 'core-js/fn/object/assign';
import {map} from 'lodash'
import $ from 'jquery'
import urlEncode from '../common/urlEncode'
import {root} from '../common/constants'
import {showArticleList, showTopArticleList,refreshArticleList} from '../article/article'

$(function(){
  $('#searchForm').bind('submit',function (e) {
    e.preventDefault();
    window.location.href =`search.html?keyword=${encodeURIComponent($('#searchForm > .si').val())}`;
    console.log("youyyyy sumt",$('#searchForm > .si').val())
  })

  showTopArticleList();
  loaded()
  refreshArticleList(curPage, {}, ()=> {
    pullDown.innerHTML = '';
    setTimeout(function () { myScroll.refresh(); }, 0);
  });

  //myScroll.refresh();
});



var myScroll;
var pullDownFlag,pullUpFlag;
var pullDown,pullUp;
var spinner;
var curPage=1;
function positionJudge(){
  if(this.y>40){    //
    pullDown.innerHTML = "";
    console.log('');
    pullDownFlag = 1;
  }else if(this.y<(this.maxScrollY-40)){   //
    pullUp.innerHTML = "";
    console.log('');
    pullUpFlag = 1;
  }
}
function action(){
  if(pullDownFlag==1){
    pullDownAction();
    pullDown.innerHTML = `<img src="../../images/loading.gif"/>`;
    console.log('');
    pullDownFlag = 0;
  }else if(pullUpFlag==1){
    pullUpAction();
    pullUp.innerHTML = `<img src="../../images/loading.gif"/>`;
    console.log('');
    pullUpFlag = 0;
  }
}
function loaded(){
  pullDownFlag = 0;
  pullUpFlag = 0;
  pullDown = document.getElementById("pullDown");
  pullUp = document.getElementById("pullUp");
  myScroll = new IScroll("#wrapper",{
    probeType: 3,
//        momentum: false,//
    mouseWheel: true,//
    //scrollbars: true,//
    fadeScrollbars: true,//
    interactiveScrollbars: true,//
    shrinkScrollbars: 'scale', // 
    useTransform: true,//CSS
    useTransition: true,//CSS
    bounce: true,//
    freeScroll: false,//
    startX: 0,
    startY: 0,
    tap:true,//tap
    preventDefault: false,
    preventDefaultException: { tagName: /^(INPUT|TEXTAREA|BUTTON|SELECT|A)$/ },  //A
//        snap: "li",// li 
  });
  myScroll.on('scroll',positionJudge);
  myScroll.on("scrollEnd",action);
}
function pullDownAction(){
  curPage=1;
  var queryTopReady=false;
  var queryArticleReady=false;
  showTopArticleList(()=>{
    queryTopReady=true;
    if(queryTopReady && queryArticleReady){
      pullDown.innerHTML = '';
    }
    setTimeout(function () { myScroll.refresh(); }, 0);
  });
  refreshArticleList(curPage, {}, ()=> {
    queryArticleReady=true;
    if(queryTopReady && queryArticleReady){
      pullDown.innerHTML = '';
    }
    setTimeout(function () { myScroll.refresh(); }, 0);
  });
}
function pullUpAction(){
  curPage++;
  showArticleList(curPage, {}, (page)=> {
    pullUp.innerHTML = '';
    if(curPage>page.totalPage){
      curPage=page.totalPage;
      pullUp.innerHTML = '';
    }
    setTimeout(function () { myScroll.refresh(); }, 0);
  });
}
function updatePosition(){
  pullDown.innerHTML = this.y>>0;
}
document.addEventListener('touchmove', function (e) {
  e.preventDefault();
}, false);



